import { Injectable } from "@angular/core";
import { environment } from "../../../environments/environment";
import { HttpClient } from "@angular/common/http";

@Injectable({
	providedIn: "root",
})
export class ChallengeBriefService {
	api_url = environment.api_url;
	constructor(private http: HttpClient) {}

	async getChallengeBrief(id: number) {
		return await this.http
			.post(this.api_url + `/anchors/select`, {
				slug: {
					id: id,
				},
			})
			.toPromise();
	}

	async updateChallengeBrief(postData: any) {
		return await this.http.post(this.api_url + `/anchors/update`, postData).toPromise();
	}

	async sendNotifications(anchorId: number) {
		return await this.http
			.post(this.api_url + "/anchors/new_anchor_notification", {
				slug: { id: anchorId },
			})
			.toPromise();
	}
}
