import { Component, OnInit } from "@angular/core";
import { DashboardService } from "./dashboard.service";
import { Select } from "@ngxs/store";
import { LoggedInUser } from "../../state/model/loggedinuser.model";
import { Observable } from "rxjs";
import { LoggedInUserState, LoggedInUserStateModel } from "src/app/state/loggedinuser.state";
import { Router } from "@angular/router";

@Component({
	selector: "app-dashboard",
	templateUrl: "./dashboard.component.html",
	styleUrls: ["./dashboard.component.css"],
})
export class DashboardComponent implements OnInit {
	@Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
	loggedInUser: LoggedInUser;

	allChallenges: any;
	awaitingApproval: any[];
	acceptingSolutions: any[];
	awaitingProposals: any[];
	Pilots: any[];
	isLoading: boolean;

	query: any = {
		filter: {},
		sort: "created_at DESC",
		pg: {
			limit: 20,
			skip: 0,
		},
	};
	uns_chall: Object;

	constructor(private dashService: DashboardService,private router:Router) {
		this.loggedInUser$.subscribe((resp) => {
			this.loggedInUser = resp.LoggedInUser;
			this.query.filter.frg_seeker_id = this.loggedInUser.userTypeId;
		});
	}
	ngOnInit() {
		this.fetchChallenges();
	}

	async fetchChallenges() {
		try {
			this.isLoading = true;
			this.allChallenges = await this.dashService.getChallenges(this.query);
			// console.log(this.allChallenges)
			this.awaitingApproval = this.allChallenges.data.filter((cl) => cl.challenge_status === "Awaiting Approval" || cl.status === 'pending');
			this.acceptingSolutions = this.allChallenges.data.filter((cl) => cl.challenge_status === "Accepting Solutions");
			this.awaitingProposals = this.allChallenges.data.filter((cl) => cl.challenge_status === "Awaiting Proposal");
			this.Pilots = this.allChallenges.data.filter((cl) => cl.challenge_status === "Pilot");
			this.isLoading = false;
		} catch (err) {
			console.log(err);
		}
	}

	handlePagination(pageNumber: number) {
		this.query.pg.skip = (pageNumber - 1) * this.query.pg.limit;
		this.fetchChallenges();
	}
	gotoUnsolicited(){
		this.router.navigate(['/seeker/user/'+ this.loggedInUser.slug +'/unsolicted-proposal'])
	}
}
