import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { SeekerComponent } from "./seeker.component";
import { SeekerPublicProfileComponent } from "./seeker-public-profile/seeker-public-profile.component";
import { SeekerUserProfileComponent } from "./seeker-user-profile/seeker-user-profile.component";
import { PermissionGuard } from "../permission.guard";
import { EditChallengeBriefComponent } from "./edit-challenge-brief/edit-challenge-brief.component";
import { SeekerEditProfileComponent } from "./seeker-edit-profile/seeker-edit-profile.component";
import { EditChallengeDirtyCheckGuard } from "./edit-challenge-brief/edit-challenge-dirty-check.guard";
import { DashboardComponent } from "./dashboard/dashboard.component";
import { MyChallengesComponent } from "./my-challenges/my-challenges.component";
import { UnsolicitedProposalsComponent } from "./unsolicited-proposals/unsolicited-proposals.component";

const routes: Routes = [
	{
		path: "",
		component: SeekerComponent,
		children: [
			{
				path: "",
				redirectTo: "/discover/providers",
				pathMatch: "full",
			},
			{
				path: ":slug",
				component: SeekerPublicProfileComponent,
			},
			{
				path: "user/:slug",
				component: SeekerUserProfileComponent,
				canActivate: [PermissionGuard],
				data: {
					permission: ["seeker"],
				},
			},
			{
				path: "user/:slug/challenge/:challengeId/edit",
				component: EditChallengeBriefComponent,
				canActivate: [PermissionGuard],
				canDeactivate: [EditChallengeDirtyCheckGuard],
				data: {
					permission: ["seeker"],
				},
			},
			{
				path: "user/:slug/edit",
				component: SeekerEditProfileComponent,
				canActivate: [PermissionGuard],
				data: {
					permission: ["seeker"],
				},
			},
			{
				path: "user/:slug/my-challenges",
				component: MyChallengesComponent,
				canActivate: [PermissionGuard],
				data: {
					permission: ["seeker"],
				},
			},
			{
				path: "user/:slug/dashboard",
				component: DashboardComponent,
				canActivate: [PermissionGuard],
				data: {
					permission: ["seeker"],
				},
			},
			{
				path: "user/:slug/dashboard/challenge/:id",
				loadChildren: () => import("./challenge-dashboard/challenge-dashboard.module").then((m) => m.ChallengeDashboardModule),
				canActivate: [PermissionGuard],
				data: {
					permission: ["seeker"],
				},
			},
			{
				path: "user/:slug/unsolicted-proposal",
				component: UnsolicitedProposalsComponent,
				canActivate: [PermissionGuard],
				data: {
					permission: ["seeker"],
				},
			},
		],
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class SeekerRoutingModule {}
