import { Component, OnInit } from "@angular/core";
import { Validators, FormGroup, FormBuilder } from "@angular/forms";
import { CustomvalidationService } from "../../services/customvalidation.service";
import { ToastrService } from "ngx-toastr";
import { EditService } from "./edit.service";
import { ActivatedRoute, Router } from "@angular/router";
import { environment } from "../../../environments/environment";
import { LocationStrategy } from "@angular/common";
import { Store } from "@ngxs/store";
import { UpdateLoggedInUser } from "../../state/action/loggedinuser.action";
@Component({
	selector: "app-edit-profile",
	templateUrl: "./edit-profile.component.html",
	styleUrls: ["./edit-profile.component.css"],
})
export class EditProfileComponent implements OnInit {
	editProfile: FormGroup;
	smartCities: any;

	profile: any;
	currentUserID: any;

	slug: string;
	serverUrl = environment.server_url;
	avatar: File;

	loading: boolean = false;

	constructor(
		private fb: FormBuilder,
		private customValidator: CustomvalidationService,
		private toastr: ToastrService,
		private _es: EditService,
		private route: ActivatedRoute,
		private router: Router,
		private locationStrategy: LocationStrategy,
		private _store: Store
	) {
		history.pushState(null, null, window.location.href);
		this.locationStrategy.onPopState(() => {
			history.pushState(null, null, window.location.href);
		});
		this.route.params.subscribe((params) => (this.slug = params.slug));
		this.editProfile = this.fb.group({
			fullname: ["", [Validators.required, Validators.pattern("[a-zA-Z .]*")]],
			email: ["", [Validators.required, Validators.email], this.customValidator.emailAvailabilityValidator.bind(this.customValidator)],
			phnum: ["", [Validators.required, Validators.pattern("(0|91)?[5-9][0-9]{9}")], this.customValidator.phoneAvailabilityValidator.bind(this.customValidator)],
			city: ["", [Validators.required, Validators.min(1)]],
			department: [""],
			designation: [""],
		});
	}

	async ngOnInit() {
		this.listSmartCities();
		this.getProfileDetails();
	}

	async getProfileDetails() {
		this.profile = await this._es.getProfile(this.slug);
		this.currentUserID = this.profile.data.frg_user_id.id || this.profile.data.frg_user_id;
		this.editProfile.patchValue({
			fullname: this.profile.data.fullname,
			email: this.profile.data.frg_user_id.email,
			phnum: this.profile.data.frg_user_id.mobile,
			city: this.profile.data.frg_smart_city_id.id,
			department: this.profile.data.department,
			designation: this.profile.data.designation,
		});
	}

	async listSmartCities() {
		try {
			this.smartCities = await this._es.getSmartCities();
			// console.log(this.smartCities);
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	async updateProfile() {
		try {
			let userData: any;
			if (this.editProfile.valid) {
				userData = await this._es.updateUser({
					slug: {
						id: this.profile.data.frg_user_id.id || this.profile.data.frg_user_id,
					},
					data: {
						email: this.editProfile.value.email,
						mobile_code: "+91",
						mobile: this.editProfile.value.phnum,
					},
				});
				this.profile = await this._es.updateProfile({
					slug: {
						id: this.profile.data.id,
					},
					data: {
						fullname: this.editProfile.value.fullname,
						email: this.editProfile.value.email,
						mobile_code: "+91",
						mobile: this.editProfile.value.phnum,
						frg_smart_city_id: this.editProfile.value.city,
						department: this.editProfile.value.department,
						designation: this.editProfile.value.designation,
					},
				});

				await Promise.all([this.saveAvatar(this.profile.data.id)]);
				let updatedUser: any = await this._es.getProfilebyID(this.profile.data.id);
				if (!this.editProfile.pristine || this.avatar) {
					this.loading = true;
					this._store.dispatch(
						new UpdateLoggedInUser({
							id: updatedUser.data.frg_user_id,
							email: updatedUser.data.email,
							name: updatedUser.data.fullname,
							userType: "seeker",
							userTypeId: updatedUser.data.id,
							avatar: `${this.serverUrl}` + updatedUser.data.avatar,
							slug: updatedUser.data.slug,
							isLoggedIn: true,
						})
					);
					this.loading = false;
					this.toastr.success("", "Profile updated successfully");
					window.history.replaceState({}, "", `/seeker/user/${this.profile.data.slug}`);
					this.router.navigate([`seeker/user/${this.profile.data.slug}/edit`]).then((resp) => {
						window.location.reload();
					});
				}
			} else {
				this.loading = false;
				this.toastr.error("", "Please provide the required values to proceed further");
			}
		} catch (err) {
			console.log(err);
			this.loading = false;
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	handleAvatar(file) {
		this.avatar = file;
	}

	async saveAvatar(id) {
		if (this.avatar) {
			let formData = new FormData();
			formData.append("slug", id);
			formData.append("avatar", this.avatar, this.avatar.name);
			await this._es.uploadAvatar(formData);
		}
	}
}
