import { Injectable } from "@angular/core";
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, CanDeactivate } from "@angular/router";
import { Observable } from "rxjs";
import { EditChallengeBriefComponent } from "./edit-challenge-brief.component";

@Injectable({
	providedIn: "root",
})
export class EditChallengeDirtyCheckGuard implements CanDeactivate<EditChallengeBriefComponent> {
	canDeactivate(component: EditChallengeBriefComponent, next: ActivatedRouteSnapshot) {
		if (
			component.challengeBriefData.data.status === "draft" &&
			(component.editChallengeForm.Step1.dirty || component.editChallengeForm.Step2.dirty || component.editChallengeForm.Step3.dirty)
		)
			component.ConfirmLeaveComponent();
		else return true;
	}
}
