import { Component, Input, OnChanges, OnInit, Output, EventEmitter } from "@angular/core";
import { ChallengeSchemaService } from "../../challenge-schema.service";
import { ToastrService } from "ngx-toastr";
declare var $;
@Component({
	selector: "challenge-schema-item",
	templateUrl: "./challenge-schema-item.component.html",
	styleUrls: ["./challenge-schema-item.component.css"],
})
export class ChallengeSchemaItemComponent implements OnInit, OnChanges {
	@Input() Id: number;
	@Input() data: any = null;
	@Input() viewType: string = "public";
	@Output() onChallengeClose = new EventEmitter<any>();
	@Output() onDelete = new EventEmitter<any>();
	closeLoading: boolean;
	deleteLoading: boolean;

	constructor(private challengeService: ChallengeSchemaService, private toastr: ToastrService) {}

	ngOnInit() {}
	ngOnChanges() {}

	async handleCloseChallenge() {
		try {
			this.closeLoading = true;
			let closedChallenge: any = await this.challengeService.updateChallengeBrief({
				slug: { id: this.data.id },
				data: {
					status: "Closed",
					challenge_status: "Closed",
				},
			});
			await this.challengeService.pushClosedNotification(this.data.id);
			this.closeLoading = false;
			$(`#closeChallenge_${this.Id}`).modal("hide");
			this.toastr.info("Challenge has been closed", "");
			this.onChallengeClose.emit(closedChallenge);
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	async deleteChallenge() {
		try {
			this.deleteLoading = true;
			let deletedData: any = await this.challengeService.deleteChallengeBrief(this.data.id);
			$(`#deleteChallenge_${this.Id}`).modal("hide");
			this.deleteLoading = false;
			this.toastr.info("", "Challenge has been deleted");
			this.onDelete.emit(deletedData);
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}
}
