import { Injectable } from "@angular/core";
import { environment } from "../../../environments/environment";
import { HttpClient } from "@angular/common/http";
@Injectable({
	providedIn: "root",
})
export class DashboardService {
	api_url = environment.api_url;
	constructor(private http: HttpClient) {}

	async getChallenges(query: any) {
		const limit = parseInt(query.pg.limit);
		const page = parseInt(query.pg.skip) / limit + 1;
		const dataResponse: any = await this.http.post(this.api_url + `/anchors/select_mul`, query).toPromise();
		Promise.all(
			dataResponse.data.map(async (challenge) => {
				// console.log("service", challenge);
				let sector: any = await this.http.post(this.api_url + `/challenge_sectors/select`, { slug: { id: challenge.frg_challenge_id.frg_challenge_sector_id } }).toPromise();
				challenge.frg_challenge_id.frg_challenge_sector_id = sector.data;
				challenge.proposal_received = challenge.proposals.length;
				challenge.active_pilots = challenge.proposals.filter((p) => p.pilot_status === "Completed").length;
			})
		);
		const countResponse: any = await this.http.post(this.api_url + `/anchors/count`, query).toPromise();
		let totalResults = countResponse.data;
		let startItem = totalResults > 0 ? (page - 1) * limit + 1 : 0;
		let endItem = totalResults > 0 ? startItem - 1 + dataResponse.data.length : 0;
		let PageCount = Math.ceil(countResponse.data / limit);
		dataResponse.currently_showing = { from: startItem, to: endItem };
		dataResponse.total_results = totalResults;
		dataResponse.page_count = PageCount;
		return dataResponse;
	}

	async getSeekerDetails(id){
		var post_data ={
			slug:{
				id:id
			}
		}
		return await this.http.post(this.api_url + '/seekers/select_slug',post_data).toPromise()
	}

	async getUnsolicitedChallenges(post_data:any){

		const limit = post_data.pg.limit;
    	const page = post_data.pg.skip / limit + 1;

		let resp_data:any = await this.http.post(this.api_url + '/unsolicited_proposal/select_mul',post_data).toPromise();
		let totalResults: any = await this.http .post(this.api_url + "/unsolicited_proposal/count", post_data).toPromise();

		let startItem = totalResults.data > 0 ? (page - 1) * limit + 1 : 0;
		let endItem =
			totalResults.data > 0 ? startItem - 1 + resp_data.data.length : 0;
		let PageCount = Math.ceil(totalResults.data / limit);
		resp_data.currently_showing = { from: startItem, to: endItem };
		resp_data.total_results = totalResults.data;
		resp_data.page_count = PageCount;
		return resp_data
	}

	async getUnsolicitedChallengesCount(post_data:any){
		return await this.http.post(this.api_url + "/unsolicited_proposal/count", post_data).toPromise();
	}

	async associateAnchorAndSmartCity(post_data:any) {
		return await this.http.post(this.api_url + "/anchors_and_smart_cities/create", post_data).toPromise();
	}

	async updateUnsolProposal(post_data:any){
		return await this.http.post(this.api_url + "/unsolicited_proposal/update", post_data).toPromise();
	}

	async createUnsoPropAnchor(post_data:any){
		return await this.http.post(this.api_url + "/unsolicited_proposal/create_anchor_update_uns_proposal", post_data).toPromise();
	}
}
