import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { ChallengeSchemaService } from "../../challenge-schema.service";
import { ToastrService } from "ngx-toastr";
declare var $;
@Component({
	selector: "challenge-draft-item",
	templateUrl: "./challenge-draft-item.component.html",
	styleUrls: ["./challenge-draft-item.component.css"],
})
export class ChallengeDraftItemComponent implements OnInit {
	@Input() Id: number;
	@Input() data: any = null;
	@Output() onDelete = new EventEmitter<any>();
	deleteLoading: boolean;
	constructor(private challengeService: ChallengeSchemaService, private toastr: ToastrService) {}

	ngOnInit() {}

	async deleteChallengeDraft(draftId: number) {
		try {
			this.deleteLoading = true;
			let deletedData: any = await this.challengeService.deleteChallengeBrief(draftId);
			$(`#deleteChallengeDraft_${this.Id}`).modal("hide");
			this.deleteLoading = false;
			this.toastr.info("", "Draft deleted");
			this.onDelete.emit(deletedData);
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}
}
