import { Injectable } from "@angular/core";
import { environment } from "../../../environments/environment";
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs";

@Injectable({
	providedIn: "root",
})
export class EditService {
	api_url = environment.api_url;

	constructor(private http: HttpClient) {}

	async getSmartCities() {
		return await this.http
			.post(this.api_url + "/smart_cities/select_mul", {
				sort: [{ name: "ASC" }],
			})
			.toPromise();
	}
	async changePassword(postdata: any) {
		return await this.http.post(this.api_url + "/users/change_password", postdata).toPromise();
	}
	async getProfile(slug: string) {
		return await this.http.post(this.api_url + "/seekers/select", { search: { slug: slug } }).toPromise();
	}
	async getProfilebyID(id: any) {
		return this.http.post(this.api_url + "/seekers/select", { search: { id: id } }).toPromise();
	}
	async updateProfile(postdata: any) {
		return await this.http.post(this.api_url + "/seekers/update", postdata).toPromise();
	}
	async updateUser(postdata: any) {
		return await this.http.post(this.api_url + "/users/update", postdata).toPromise();
	}
	async uploadAvatar(postData) {
		return await this.http.post(this.api_url + "/seekers/upload", postData).toPromise();
	}
}
