import { Component, Input, OnInit } from "@angular/core";
import { ProfileService } from "../profile-top-card/profile.service";
import { ActivatedRoute, NavigationExtras, Router } from "@angular/router";
import { environment } from "../../../../environments/environment";

@Component({
	selector: "profile-top-card",
	templateUrl: "./profile-top-card.component.html",
	styleUrls: ["./profile-top-card.component.css"],
})
export class ProfileTopCardComponent implements OnInit {
	@Input() viewType: string = "public";
	profileInfo: any;
	profileLoading: boolean;
	userSlug: string;
	serverUrl: any = environment.server_url;
	constructor(private _profile: ProfileService, private route: ActivatedRoute, private router: Router) {
		this.route.params.subscribe((params) => (this.userSlug = params.slug));
	}

	ngOnInit() {
		this.fetchProfileInfo(this.userSlug);
	}

	async fetchProfileInfo(slug: string) {
		this.profileLoading = true;
		this.profileInfo = await this._profile.getProfile(slug);
		this.profileLoading = false;
	}

	editSectors() {
		var query_params: NavigationExtras = {
			queryParams: {
				mode: "update",
			},
		};
		// this.router.navigate(['/discover/select-sector'],{
		//   state: { mode: query_params }
		// })
		this.router.navigate(["/discover/select-sector"], query_params);
	}
}
