import { Injectable } from "@angular/core";
import { environment } from "../../environments/environment";
import { HttpClient } from "@angular/common/http";

@Injectable({
	providedIn: "root",
})
export class ChallengeSchemaService {
	api_url = environment.api_url;

	constructor(private http: HttpClient) {}

	async getChallengeSchemas(seekerId: number, Currentpage: number, filterBy?: string) {
		const limit = 10;
		const page = Currentpage / limit + 1;
		const query = {
			filter: {
				status: ["active", "closed"],
				frg_seeker_id: seekerId,
				challenge_status: "",
			},
			pg: {
				skip: Currentpage,
				limit: limit,
			},
		};
		if (filterBy != "" && typeof filterBy != undefined) {
			query.filter.challenge_status = filterBy;
		} else {
			delete query.filter.challenge_status;
		}
		const dataResponse: any = await this.http.post(this.api_url + `/anchors/select_mul`, query).toPromise();
		const countResponse: any = await this.http.post(this.api_url + `/anchors/count`, query).toPromise();
		let totalResults = countResponse.data;
		let startItem = totalResults > 0 ? (page - 1) * limit + 1 : 0;
		let endItem = totalResults > 0 ? startItem - 1 + dataResponse.data.length : 0;
		let PageCount = Math.ceil(countResponse.data / limit);
		dataResponse.currently_showing = { from: startItem, to: endItem };
		dataResponse.total_results = totalResults;
		dataResponse.page_count = PageCount;
		return dataResponse;
	}

	async getChallengeDrafts(seekerId: number, Currentpage: number) {
		const limit = 10;
		const page = Currentpage / limit + 1;
		const query = {
			filter: {
				status: "draft",
				frg_seeker_id: seekerId,
			},
			pg: {
				skip: Currentpage,
				limit: limit,
			},
		};
		const dataResponse: any = await this.http.post(this.api_url + `/anchors/select_mul`, query).toPromise();
		const countResponse: any = await this.http.post(this.api_url + `/anchors/count`, query).toPromise();
		let totalResults = countResponse.data;
		let startItem = totalResults > 0 ? (page - 1) * limit + 1 : 0;
		let endItem = totalResults > 0 ? startItem - 1 + dataResponse.data.length : 0;
		let PageCount = Math.ceil(countResponse.data / limit);
		dataResponse.currently_showing = { from: startItem, to: endItem };
		dataResponse.total_results = totalResults;
		dataResponse.page_count = PageCount;
		return dataResponse;
	}

	async getChallenge(id: number) {
		let challenge: any = await this.http
			.post(this.api_url + `/challenges/select`, {
				slug: {
					id: id,
				},
			})
			.toPromise();
		return challenge.data;
	}

	async deleteChallengeBrief(id: number) {
		return await this.http
			.post(this.api_url + `/anchors/delete`, {
				slug: {
					id: id,
				},
			})
			.toPromise();
	}

	async updateChallengeBrief(postData: any) {
		return await this.http.post(this.api_url + `/anchors/update`, postData).toPromise();
	}

	async pushClosedNotification(id: number) {
		return await this.http
			.post(this.api_url + `/anchors/close_anchor_notification`, {
				slug: {
					id: id,
				},
			})
			.toPromise();
	}
}
