import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { ChallengeSchemaService } from "../challenge-schema.service";
import { ProfileinfoService } from "../profileinfo.service";
import { challengeStatus } from "../../const";
import { ToastrService } from "ngx-toastr";
@Component({
	selector: "app-seeker-user-profile",
	templateUrl: "./seeker-user-profile.component.html",
	styleUrls: ["./seeker-user-profile.component.css"],
})
export class SeekerUserProfileComponent implements OnInit {
	anchors: any;
	drafts: any;
	seekerId: number;
	userSlug: string;
	anchorLoading: boolean;
	draftLoading: boolean;
	challengeFilter: string[] = challengeStatus;
	anchorData: any[];
	draftData: any[];
	constructor(private _challenge: ChallengeSchemaService, private _profile: ProfileinfoService, private route: ActivatedRoute, private toastr: ToastrService) {
		this.route.params.subscribe((params) => (this.userSlug = params.slug));
	}

	async ngOnInit() {
		this.anchorLoading = true;
		let resp: any = await this._profile.getProfile(this.userSlug);
		this.seekerId = resp.id;
		await Promise.all([this.fetchAnchors(0), this.fetchChallengeDrafts(0)]);
	}

	async fetchAnchors(page: number, filterBy?: string) {
		try {
			this.anchorLoading = true;
			this.anchors = await this._challenge.getChallengeSchemas(this.seekerId, page, filterBy);
			let anchorsWithChallenge = this.anchors.data.map(async (el) => {
				return new Promise(async (resolve) => {
					let challengeId = el.frg_challenge_id.id;
					el.frg_challenge_id = await this._challenge.getChallenge(challengeId);
					resolve(el);
				});
			});
			this.anchorData = await Promise.all(anchorsWithChallenge);
			this.anchorLoading = false;
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	async fetchChallengeDrafts(page: number) {
		try {
			this.draftLoading = true;
			this.drafts = await this._challenge.getChallengeDrafts(this.seekerId, page);
			let anchorsWithChallenge = this.drafts.data.map(async (el) => {
				return new Promise(async (resolve) => {
					let challengeId = el.frg_challenge_id.id;
					el.frg_challenge_id = await this._challenge.getChallenge(challengeId);
					resolve(el);
				});
			});
			this.draftData = await Promise.all(anchorsWithChallenge);
			this.draftLoading = false;
		} catch (err) {
			console.log(err);
			this.toastr.error("", "Something went wrong please try again");
		}
	}

	async handleDraftDelete(deletedDraft) {
		await this.fetchChallengeDrafts(0);
	}

	async handleStatusFilter(e) {
		await this.fetchAnchors(0, e.target.value);
	}

	async handlePagination(pageIndex) {
		let nextPage = (pageIndex - 1) * 10;
		await this.fetchAnchors(nextPage);
	}

	async handleDraftPagination(pageIndex) {
		let nextPage = (pageIndex - 1) * 10;
		await this.fetchChallengeDrafts(nextPage);
	}

	async handleClosedChallenge(closedChallenge) {
		this.fetchAnchors(0);
	}

	async handleDeleteChallenge(deletedChallenge) {
		this.fetchAnchors(0);
	}
}
