import { Component, Input, OnInit } from '@angular/core';
import { ChallengeService } from 'src/app/challenge/challenge.service';
import { Select } from "@ngxs/store";
import { LoggedInUserState, LoggedInUserStateModel } from 'src/app/state/loggedinuser.state';
import { Observable } from 'rxjs';
import { LoggedInUser } from 'src/app/state/model/loggedinuser.model';
import { DashboardService } from '../dashboard/dashboard.service';
import { ToastrService } from "ngx-toastr";
import { environment } from "../../../environments/environment";
import * as saveAs from 'file-saver';
import { Router } from '@angular/router';

@Component({
  selector: 'app-unsolicited-proposals',
  templateUrl: './unsolicited-proposals.component.html',
  styleUrls: ['./unsolicited-proposals.component.css']
})

export class UnsolicitedProposalsComponent implements OnInit {

  @Select(LoggedInUserState) loggedInUser$: Observable<LoggedInUserStateModel>;
  loggedInUser: LoggedInUser;
  serverUrl: string = environment.server_url;
  uns_chall: any;
  query: any;
  rec_count:any;
  post_data:any ={
    "filter": {},
    "pg": {
      limit: 10,
      skip: 0
    }
  };

  constructor(private _as: ChallengeService, private _ds: DashboardService, private toastr: ToastrService,private router:Router) {
    this.loggedInUser$.subscribe((resp) => {
      this.loggedInUser = resp.LoggedInUser;
    });
  }

  ngOnInit() {
    this.getUnsChallenges();
  }

  async getUnsChallenges() {


		try {
			let seeker_det: any = await this._ds.getSeekerDetails(this.loggedInUser.userTypeId);
			if (seeker_det.status == true) {
        this.post_data.filter.frg_smart_city_id = seeker_det.data.frg_smart_city_id.id,
        this.post_data.filter.sol_sub_status = false
				let resp: any = await this._ds.getUnsolicitedChallenges(this.post_data);
				if (resp.data.length) {
					this.uns_chall = resp;
				}
			}


		} catch (err) {
			console.log(err);
		}
	}

  async changeStatus(status: string, challenge_id: string, city_id: string, uns_id: string,item:any) {
    var timestamp = new Date()

    if (status == 'approved') {
      var post_data = {
        data: {
          status: "approved",
          challenge_summary: item.frg_challenge_id.name + timestamp ,
          frg_seeker_id: this.loggedInUser.userTypeId,
          frg_challenge_id: challenge_id,
          info_links: ["", "", ""],
          frg_smart_city_id: city_id,
          uns_id: uns_id
        }
      }
    }
    else {
      var post_data = {
        data: {
          status: "rejected",
          challenge_summary: item.frg_challenge_id.name + timestamp ,
          frg_seeker_id: this.loggedInUser.userTypeId,
          frg_challenge_id: challenge_id,
          info_links: ["", "", ""],
          frg_smart_city_id: city_id,
          uns_id: uns_id
        }
      }
    }

    let resp: any = await this._ds.createUnsoPropAnchor(post_data);
    if (resp.status == true) {
      this.toastr.success("", "Anchor created successfully");
      this.router.navigate(['/seeker/user/' + this.loggedInUser.userTypeId + '/dashboard/challenge/'+ resp.data[0].frg_anchor_id +'/eoi'])
    }
    else {
      this.toastr.error("", "Something went wrong , please try again")
    }

  }

  downloadFiles(url) {
    saveAs.saveAs(this.serverUrl + url);
  }

  handlePagination(pageNumber: number) {
		this.post_data.pg.skip = (pageNumber - 1) * this.post_data.pg.limit;
		this.getUnsChallenges();
	}

  backClicked(){
    this.router.navigate(['/seeker/user/'+this.loggedInUser.slug+'/dashboard'])
  }


}
